<template>
  <div class="home">
    <section class="hero mb-6">
    <div class="hero-body has-text-centered">
    <p class=" has-light-text title  mb-6">
      Welcome to <img class="guitar-logo" src="@/assets/rng.png">
    </p>
  
    </div>
    </section>
</div>
</template>

<script>
import axios from 'axios'

import ProductBox from '@/components/ProductBox.vue'

export default {
  name: 'HomeView',
  components: {
    ProductBox,
  },
  data() {
    return {
      latestProducts: []
    }
  },

  mounted() {
    this.getLatestProduct()

    document.title = 'RockNGuitars' 
  },
  methods: {
    async getLatestProduct() {
      this.$store.commit('setIsLoading', true)

      await axios
      .get('/api/v1/latest-products/')
      .then(response => {
      this.latestProducts = response.data
      })
      .catch(error => {
        console.log(error)
      })

      this.$store.commit('setIsLoading', false)
    }
  }
}
</script>

<style scoped>
 .guitar-logo {
  height: 6rem;
 }
 .title {
  color: #ccc;
 }

  </style>